.footer {
  padding-top: 310px;
  background-image: url(../img/footer-bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #fff;

  @include mobile {
    padding-top: 200px;
  }

  &__top  {
    display: flex;
    gap: 128px;
    padding-bottom: 52px;
    border-bottom: 1px solid #5d6544;

    @include mobile {
      gap: 50px;
      padding-bottom: 25px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;

    &-text {
    font-weight: 400;
    font-size: 16px;
    line-height: 40px;
    color: #b6c197;

    @include mobile {
      font-size: 10px;
      line-height: 30px;
    }
    }

    &-value {
      font-style: normal;
      font-weight: 700;
      font-size: 16px;
      line-height: 40px;
      color: #fff;
      text-decoration: none;

      @include mobile {
        font-size: 14px;
        line-height: 30px;
      }
    }
  }

  &__middle {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 70px;
    padding: 60px 0;
    border-bottom: 1px solid #5d6544;

    @include medium-tablet {
      grid-template-columns: repeat(2,1fr);
    }

    @include mobile {
      grid-template-columns: repeat(1,1fr);
      gap: 20px;
      padding: 35px 0;
    }
  }


  &__title {
    margin: 0;
    margin-bottom: 21px;
    font-weight: 600;
    font-size: 16px;
    line-height: 38px;
    color: #fff;
    text-align: center;

    @include mobile {
      margin-bottom: 6px;
      line-height: 25px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 40px;
    color: #b6c197;
    transition: color .2s ease-in-out;

    &:hover {
      color: #ffd723;
    }
  }

  &__col {
    @include mobile {
      align-items: center;
      text-align: center;
    }
  }

  &__col:nth-child(3) {
    border-right: 1px solid #5d6544;

    @include medium-tablet {
      border: none;
    }
  }



  &__text {
    margin: 0;
    margin-bottom: 23px;
    max-width: 270px;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: #b6c197;

    @include mobile {
      margin-left: 45px;
    }
  }

  &__mail {
    display: inline-block;
    margin-bottom: 34px;
    font-weight: 700;
    font-size: 24px;
    line-height: 38px;
    color: #fff;
  }

  &__bottom {
    padding-top: 25px;
    padding-bottom: 30px;

    @include flex-all-sb;

    @include mobile {
      flex-direction: column;
    }
  }

}

.copyright {
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #b5c096;
}
