.hero {
  padding-top: 188px;
  padding-bottom: 210px;
  background-image: url(../img/hero-bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  &__content {
    max-width: 606px;
  }

  &__subtitle {
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: var(--light-color);
  }

  &__title {
    margin: 22px 0;
    font-weight: 700;
    font-size: 60px;
    line-height: 74px;
    color: var(--light-color);

    @include small-tablet {
      font-size: 45px;
      line-height: 60px;
    }

    @include mobile {
      font-size: 30px;
      line-height: 45px;
      max-width: 350px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 45px;
    font-weight: 400;
    font-size: 16px;
    line-height: 26px;
    color: var(--light-color);

    @include mobile {
      max-width: 350px;
    }
  }
}
