.btn {
  border-bottom: 1px solid #d4d6d4;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #d4d6d4;
  fill: currentColor;
  transition: all .3s ease;
  padding: 3px 0;

  svg {
    width: 10px;
    height: 10px;
    margin-right: 5px;
  }

  &:hover {
    color: #ffd723;
    border-bottom: 1px solid #ffd723;
  }

  &--hero {
    color: #ffd723;
    border-bottom: 1px solid #ffd723;
  }
}
