.offer {
  position: relative;
  top: -50%;
  transform: translateY(50%);
  width: 88%;
  padding: 70px 0;
  background-color: #fafafa;
  box-shadow: 50px 130px 100px rgba(0, 0, 0, 0.145947);

  @include medium-tablet {
    padding: 50px 0;
  }

  &__title {
    max-width: 822px;
    margin: 0;
    margin-bottom: 50px;
    font-weight: 700;
    font-size: 60px;
    line-height: 74px;
    color: #4D533C;

    @include medium-tablet {
      font-size: 40px;
      line-height: 54px;
    }

    @include mobile {
      font-size: 30px;
      line-height: 44px;
      margin-bottom: 25px;
    }
  }

  &__btn {
    text-transform: uppercase;
  }
}
