.form__container {
  max-width: 500px;
}

.form__title {
  text-align: center;
  color: #4d533c;
}

.form__btn {
  display: block;
  margin: 0 auto;
  background-color: #4d533c;
  padding: 10px;
  border-radius: 40px;
  color: #fff;
}

.info {
  display: flex;
  flex-direction: column;
  gap: 40px;
  justify-content: center;

  input {
    padding: 7px 0;
    text-align: center;
    width: 80%;
    margin: 0 auto;
    border: none;
    border-bottom: 1px solid #4d533c;


    &:last-child {
      margin-bottom: 20px;
    }
  }
}
