.burger {
  --offset-horisontal: 11px;
  --offset-vertical: 14px;
  position: relative;
  border-radius: 100%;
  padding: var(--offset-vertical) var(--offset-horisontal);
  width: 40px;
  height: 40px;
  z-index: 1000;
  background-color:#f5f6f7;
  opacity: 1;
  transition: all .2s ease-in-out;


  &__line {
    position: absolute;
    top: 13px;
    left: 50%;
    transform: translateX(-50%);
    width: 18px;
    height: 2px;
    background-color: #000;
    transition: all .2s ease-in-out;
  }

  &::before, &::after {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 18px;
    height: 2px;
    background-color: #000;
    transition: all .2s ease-in-out
  }

  &::before {
    top: 20px;
  }

  &::after {
    top: 27px;
  }

}

.burger--active {
  opacity: 0;
  transition: opacity 0.6s ease-in-out;

  @include mobile {
    opacity: 1;
  }
}

