.social {

  display: flex;
  gap: 11px;
  align-items: center;

  @include mobile {
    justify-content: center;
  }

  &__item {
    width: 33px;
    height: 33px;
    border-radius: 100%;
    background-color: #444a34;
    transition: all .2s ease-in-out;

    &:hover {
      background-color: #ffd723;
    }
  }

  &__link {
    display: block;
    text-align: center;
    margin-top: 6px;
    fill: #50583b;
    transition: all .2s ease-in-out;

    svg {
      width: 19px;
      height: 19px;
    }

    &:hover {
      fill: #fff;
    }
  }
}
