.nav {
  position: fixed;
  right: 0;
  top: 0;
  height: 100vh;
  z-index: 100;
  background-color: rgba(255, 255, 255, 0.1);
  transform: translateX(100%);
  transition: transform 0.6s ease-in-out;

  @include mobile {
    width: 100%;
    overflow-y: auto;
    background-color: #b5c096;
  }

  &__list {
    padding-top: 60px;
    padding-right: 80px;
    padding-left: 70px;

    @include mobile {
      padding-top: 100px;
      text-align: center;
    }
  }

  &__item {
    margin-bottom: 30px;
  }

  &__link {
    font-weight: 700;
    font-size: 30px;
    line-height: 18px;
    color: #d4d6d4;
    transition: all .3s ease;

    &:hover {
      color: #ffd723;
    }
  }


}

.menu--active {
  transform: none;
}
