.services {
  margin-bottom: -180px;
  padding: 150px 0;

  @include medium-tablet {
    padding: 100px 0;
  }

  @include mobile {
    padding: 60px 0;
  }



  &__item {
    display: flex;
    flex-direction: column;
    padding: 27px 30px 0;

    @include mobile {
      margin: 0 auto;
      padding: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 60px;
    max-width: 607px;
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;
    color: #4d533c;

    @include mobile {
      font-size: 30px;
      line-height: 40px;
      max-width: 500px;
    }
  }

  &__list {
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(4,1fr);

    @include medium-tablet {
      grid-template-columns: repeat(2,1fr);

    }

    @include mobile {
      grid-template-columns: repeat(1,1fr);
      gap: 50px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    max-width: 183px;
    font-weight: 500;
    font-size: 24px;
    line-height: 34px;
    text-transform: uppercase;
    color: #4D533C;

    @include mobile {
      font-size: 20px;
      line-height: 30px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 56px;
    max-width: 194px;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    color: rgba(33, 56, 66, 0.6);

    @include mobile {
      margin-top: auto;
    }
  }

  &__btn {
    display: inline-flex;
    align-items: center;
    margin-right: auto;
    margin-bottom: 0;
    margin-top: auto;
  }
}
